import { ControleMetasService } from './../services/dados/controle-metas.service';
import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

import Chart from 'chart.js';

@Component({
  selector: 'app-grafico',
  templateUrl: './grafico.component.html',
  styleUrls: ['./grafico.component.css']
})
export class GraficoComponent implements OnInit {
  @ViewChild('canvasGrafico') canvasGrafico: ElementRef;
  @Input() dados;
  @Input() nomeGrupo;
  
  // private dadosTotais: Array<any>
  private totaisPorMes
  private chart = []

  constructor(private controleMetasService: ControleMetasService) { }

  ngOnInit() {

    let canvasGraficoCtx = this.canvasGrafico.nativeElement.getContext('2d');

    Chart.defaults.global.defaultFontFamily = 'SimplonBP-Regular';

    Chart.plugins.register({
      beforeDraw:(chartInstance, easing) => { // need to manipulate tooltip visibility before its drawn (but after update)
        if (chartInstance.config.options.tooltips.onlyShowForDatasetIndex) { // check and see if the plugin is active (its active if the option exists)
          var tooltipsToDisplay = chartInstance.config.options.tooltips.onlyShowForDatasetIndex; // get the plugin configuration
          var active = chartInstance.tooltip._active || []; // get the active tooltip (if there is one)
          if (active.length > 0) { // only manipulate the tooltip if its just about to be drawn
            if (tooltipsToDisplay.indexOf(active[0]._datasetIndex) === -1) { // first check if the tooltip relates to a dataset index we don't want to show
              chartInstance.tooltip._model.opacity = 0; // we don't want to show this tooltip so set it's opacity back to 0which causes the tooltip draw method to do nothing
            }
          }
        }
      }
    });

    this.chart = new Chart(canvasGraficoCtx, {
      type: 'bar',
      data: {
        labels: ['Jan','Fev','Mar','Abr','Mai','Jun','Jul','Ago','Set','Out','Nov','Dez'],
        datasets: [
          {
            type: "line",
            label: "Retenção",
            backgroundColor: "#6D3552",
            borderColor: "#6D3552",
            data: this.dados.Retenção,
            yAxisID: 'y-axis-2',
            fill: false,
            lineTension: 0
          }, {
            type: "line",
            label: "Meta          ",
            backgroundColor: "#619292",
            borderColor: "#619292",
            data: this.dados.Meta,
            yAxisID: 'y-axis-2',
            fill: false,
            borderDash: [10,5],
            lineTension: 0
          }, {
            type: 'bar',
            label: 'Entrantes',
            backgroundColor: '#A22AFF',
            data: this.dados.Entrantes,
            yAxisID: 'y-axis-1'
          }, {
            type: 'bar',
            label: 'Retidas',
            backgroundColor: '#F67006',
            data: this.dados.Retidas,
            yAxisID: 'y-axis-1'
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        tooltipEvents: [],
        showTooltips: true,
        tooltipCaretSize: 0,
        // onAnimationComplete: function () {
        //   this.showTooltip(this.segments, true);
        // },
        tooltips: { 
          mode: 'single',
          // new property from our plugin
          // configure with an array of datasetIndexes that the tooltip should display for
          // to get the datasetIndex, just use it's position in the dataset [] above in the data property
          onlyShowForDatasetIndex: [0, 1],
          callbacks: {
            label: function(tooltipItems, data) {
              console.log('tool',tooltipItems)
              console.log('data',data)
              return tooltipItems.yLabel + ' %';
            }
          }
        },
        elements: {
          line: { fill: true }
        },
        legend: {
          // display: false,
          labels: {
            boxWidth: 5,
            usePointStyle: true
          }
        },
        showAllToolTips: true,
        scales: {
          xAxes: [
            {
              barPercentage: 1.0,
              categoryPercentage: 0.6,
              gridLines: { display: false }
            }
          ],
          yAxes: [
            {
              position: "left",
              scaleLabel: "<%=value%>",
              id: "y-axis-1",
              gridLines: { display: true },
              labels: { show: true },
              ticks: {
                callback: function(value, index, values) {
                  return value.toLocaleString('pt-BR');
                }
              }
            }, {
              position: "right",
              id: "y-axis-2",
              gridLines: { display: false },
              labels: { show: true },
              ticks: { 
                beginAtZero:true,
                steps: 10,
                max: 100,
                callback: function(value, index, values) {
                  return value + ' %';
                }
              }
            }
          ]
        }
      }
    });
  }
}


