import { ControleMetasService } from '../services/dados/controle-metas.service';
import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-grid',
  templateUrl: './grid.component.html',
  styleUrls: ['./grid.component.css']
})
export class GridComponent implements OnInit {
  @Input() gridDados;
  destaquesObj : Array<Object>;
  traduzMeses : Array<string>;
  aplicacoes : Array<string>;
  anoSelecionado : number;
  metaMes : any[] = [];
  Math : Math;
  mockDados : any[] = [];
  iconsHtml : Array<string>;
  tempObject = Object;

  constructor(){  }
  ngOnInit() {
       this.traduzMeses = ['JANEIRO', 'FEVEREIRO', 'MARÇO', 'ABRIL', 'MAIO', 'JUNHO', 'JULHO', 'AGOSTO',
        'SETEMBRO', 'OUTUBRO', 'NOVEMBRO', 'DEZEMBRO'
      ];
      this.aplicacoes = ['Ícone Oi_Móvel_Pré', 'Ícone Oi_Móvel_Controle', 'Ícone Oi_Móvel_Controle', 'Ícone Oi_Móvel_Pré','Ícone Oi_Móvel_Pré','Ícone Oi_Móvel_Controle','Móvel','Banda Larga','Fixo','Tv','USSD','USSD','USSD'];
      this.anoSelecionado = 2017;
      // this.anoSelecionado = new Date().getFullYear();
      this.Math = Math;
      this.iconsHtml = ['fa fa-chevron-up', 'fa fa-caret-up', 'fa fa-caret-down'];
      this.mockDados = this.gridDados;
  }
}
