import { Component, OnInit, EventEmitter } from '@angular/core';

import { CabecalhoBotoesComponent } from '../cabecalho-botoes/cabecalho-botoes.component'

import { ExibicaoRelatorioService } from '../services/exibicao-relatorio.service'
import { ControleMetasService } from '../services/dados/controle-metas.service';

@Component({
  selector: 'app-controle-metas-relatorio',
  templateUrl: './controle-metas-relatorio.component.html',
  styleUrls: ['./controle-metas-relatorio.component.css']
})
export class ControleMetasRelatorioComponent implements OnInit {
  private dadosGrafico: Object;
  private exibeGrid:boolean = true;
  private subscriptionGrid: any;
  private exibeFiltroAplicacao:boolean = false;
  private subscriptionFiltroAplicacao: any;
  private gridDados: any;
  private retencaoDestaques:number;
  private metaDestaques:number;
  private entrantesDestaques:number;
  private retidasDestaques:number;
  private derivadasDestaques:number;

  constructor(
    private exibicaoRelatorio: ExibicaoRelatorioService,
    private controleMetasService: ControleMetasService
  ) {}


  ngOnInit() {
  	this.subscriptionGrid = this.exibicaoRelatorio.getExibeGridChange().
  		subscribe(emitted => this.exibeGrid = emitted);
    this.subscriptionFiltroAplicacao = this.exibicaoRelatorio.getExibeFiltroAplicacoesChange().
      subscribe(emitted => this.exibeFiltroAplicacao = emitted);
    this.controleMetasService.getDadosPorMesPorGrupoSemTotal()
      .subscribe((resposta:any) => {
        this.dadosGrafico = resposta;
    })
    this.controleMetasService.getDadosPorAplicacaoPorMesPorTipoMock().subscribe((valor)=>{
      for(let aplicacao in valor){
        delete valor[aplicacao]['Derivadas'];
        delete valor['Mobilidade'];
      }
      this.gridDados=valor;
    });

    this.controleMetasService.getDadosPorAplicacaoPorMesPorTipoMock()
      .subscribe((resposta: Object) => {
        let retencaoTotal = 0;
        let metaTotal = 0;
        let retidasTotal = 0;
        let derivadasTotal = 0;
        let entrantesTotal = 0;
        for ( let i=0; i<12 ; i++ ) {
          retencaoTotal += resposta['Mobilidade']['Retenção'][i];
          metaTotal += resposta['Mobilidade']['Meta'][i];
          retidasTotal += resposta['Mobilidade']['Retidas'][i];
          derivadasTotal += resposta['Mobilidade']['Derivadas'][i];
          entrantesTotal += resposta['Mobilidade']['Entrantes'][i];
        }
        this.retencaoDestaques = Math.floor(retencaoTotal/12);
        this.metaDestaques = Math.floor(metaTotal/12);
        this.entrantesDestaques = Math.floor(entrantesTotal/12);
        this.retidasDestaques = Math.floor(retidasTotal/12);
        this.derivadasDestaques = Math.floor(derivadasTotal/12);
      })
  }
}



